import React from "react";
import Carousel from "react-bootstrap/Carousel";
import "./my-carousal.style.css";
import Slide1 from "../../assets/img/carousal/carousal1.webp";
import Slide2 from "../../assets/img/carousal/carousal3.webp";
import Slide3 from "../../assets/img/carousal/carousal5.webp";
import ScrollDown from "../scroll-down/scroll-down.component";

const MyCarousal = () => {
	return (
		<div id="home">
			<Carousel controls={false} indicators interval={2500} pause={false}>
				<Carousel.Item>
					<img
						className="d-block w-100 custom-img"
						src={Slide2}
						alt="First slide"
					/>
				</Carousel.Item>
				<Carousel.Item>
					<img
						className="d-block w-100 custom-img"
						src={Slide3}
						alt="Second slide"
					/>
				</Carousel.Item>
				<Carousel.Item>
					<img
						className="d-block w-100 custom-img"
						src={Slide1}
						alt="Third slide"
					/>
				</Carousel.Item>
			</Carousel>
			<ScrollDown />
		</div>
	);
};

export default MyCarousal;
